body {
  margin: 0;
  padding: 0;
  background-color: rgb(181, 223, 195);
  font-family: sans-serif;
  height: auto;
}
.header {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
}
.questionContainer {
  display: flex;
  justify-content: center;
  margin-top: 8pxpx;
}
.anserContainer {
  display: flex;
  justify-content: center;
  margin-top: 2px;
}
.question {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  height: 150px;
  width: 150px;
  height: 150px;
  background-color: tomato;
  color: white;
  font-size: 70px;
  border: 2px solid green;
  border-radius: 5px;
}

.anser {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  width: 150px;
  height: 150px;
  background-color: blue;
  color: white;
  font-size: 100px;
  border: 2px solid green;
  border-radius: 5px;
}

.start {
  color: blue;
}

.anserContainer {
  background-color: rgb(181, 223, 195);
  display: flex;
  justify-content: center;
  margin-top: 4px;
  height: 50px;
}

input {
  width: 224px;
  font-size: 30px;
  border: 2px solid green;
  border-radius: 5px;
}
button {
  width: 70px;
  font-size: 13px;
  border: 2px solid green;
  border-radius: 5px;
}
